/* Post code CSS */
#lookup_field button {
    margin-top: 10px;
    margin-bottom: 10px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    position: relative;
    box-sizing: border-box;
    background-color: transparent;
    outline: 0;
    border: 0;
    border-radius: 0;
    padding: 0;
    cursor: pointer;
    vertical-align: middle;
    text-decoration: none;
    color: inherit;
    font-weight: 100;
    line-height: 1.7142857142857142;
    font-size: 0.875rem;
    text-transform: capitalize;
    font-family: Public Sans, sans-serif;
    min-width: 64px;
    padding: 6px 16px;
    border-radius: 3px;
    transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, border-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms, color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    color: #fff;
    background-color: #796621;
    box-shadow: 0px 3px 1px -2px rgb(145 158 171 / 20%), 0px 2px 2px 0px rgb(145 158 171 / 14%), 0px 1px 5px 0px rgb(145 158 171 / 12%);
    box-shadow: 0 8px 16px 0 rgb(121 102 33 / 24%);
    margin-right: 15px;
}

#lookup_field input {
    padding: 7px 11px !important;
    font-size: 16px;
    font-size: 16px;
    line-height: 1.5;
    border-radius: 8px;
    border-inline-end-width: 1px;
    box-sizing: border-box;
    margin: 0;
    border-radius: 3px !important;
    font-size: 14px;
    line-height: 1.5714285714285714;
    list-style: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    position: relative;
    display: inline-block;
    width: 100%;
    min-width: 0;
    background-color: #ffffff;
    background-image: none;
    border-width: 1px;
    border-style: solid;
    border-radius: 6px;
    transition: all 0.2s;
}

.idpc-select {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    color: rgba(0, 0, 0, 0.88);
    font-size: 16px;
    line-height: 1.5714285714285714;
    list-style: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, 'Noto Sans', sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol', 'Noto Color Emoji';
    display: flex;
    width: 100%;
    height: 40px;
    padding: 0 11px;
    border-radius: 3px;
}

.ant-table-content tr th:last-child {
    width: 125px;
}

